.h2Text {
  font-size: 30px;
  text-align: center;
  margin: 5% 0;
}

.modal i {
  font-size: 80px;
  border: 2px solid black;
  border-radius: 50%;
}
.modal {
  text-align: center;
}

.icon {
  background-color: #ffe600;
  padding: 4%;
}
.modal button {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 5%;
  padding: 10px 0px;
  font-size: 25px;
  background-color: #ffe600;
  border: 2px solid #ffe600;
  border-radius: 10px;
  cursor: pointer;
}

.Box {
  border-radius: 20px;
  overflow: hidden;
  border: none !important;
}
