.slii slick-next,
.slii .slick-prev {
  background-color: transparent !important;
  border: 1px solid white;
  border-radius: 50%;
  color: white;
  width: 60px;
  height: 60px;
}
.slii .slick-next {
  left: 13% !important;
  z-index: 1000 !important;
  display: none !important;
}
.slii .slick-prev {
  position: absolute !important;
  left: 6% !important;
  z-index: 1000;
  display: none !important;
}
.slii .slick-next:before,
.slii .slick-prev:before {
  font-size: 20px;
  line-height: 1;
  color: white;
  background: none;
}

.slii .slick-prev::before {
  font-family: FontAwesome;
  content: "\f060";
  font-weight: 300;
}

.slii .slick-next::before {
  font-family: FontAwesome;
  content: "\f061";
  font-weight: 300;
}
.slii .slide {
  width: 100% !important;
}
.slii .slide img {
  width: 100% !important;
}

@media screen and (max-width: 500px) {
  .slii .slick-prev {
    display: none !important;
  }

  .slii .slick-next {
    display: none !important;
  }
  .slii .slick-prev::before {
    display: none !important;
  }
  .slide img {
    width: 100% !important;
    height: 500px !important;
  }
}
.Slide {
  position: relative;
}

.Slide .next {
  position: absolute !important;
  left: 6% !important;
  top: 68%;
  border: 1px solid white !important;
  width: 60px !important;
  height: 60px !important;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  display: none;
}
.Slide .pri {
  position: absolute !important;
  color: white !important;
  border: 1px solid white !important;
  left: 0;
  width: 60px !important;
  height: 60px !important;
  padding: 15px;
  border-radius: 50%;
  top: 68%;
  left: 13.2%;
  display: none;
}
.Slide .indicators {
  position: absolute;
  top: 79%;
  left: 6.5%;
  color: white !important;
}

.Slide
  .react-slideshow-container
  + ul.indicators
  .each-slideshow-indicator:before {
  background: #fcd318 !important;
  width: 15px;
  height: 14px;
}
.backdiv {
  padding-bottom: 60px !important;
}
@media screen and (max-width: 1330px) {
  .Slide .indicators {
    position: absolute;
    top: 84%;
    left: 6%;
    color: white !important;
  }
  .Slide .next {
    left: 1% !important;
    top: 67% !important;
  }
  .Slide .pri {
    left: 9%;
    top: 67% !important;
  }
}

@media screen and (max-width: 810px) {
  .Slide .pri {
    left: 14%;
    top: 67% !important;
  }
}

@media screen and (max-width: 570px) {
  .Slide .pri {
    left: 17%;
    top: 70% !important;
  }
  .Slide .next {
    top: 70% !important;
  }
}

@media screen and (max-width: 405px) {
  .Slide .pri {
    top: 72% !important;
    left: 23% !important;
  }
  .Slide .next {
    top: 72% !important;
  }
  .Slide .indicators {
    top: 86% !important;
    left: 2% !important;
  }
  .backdi {
    padding-bottom: 1160px !important;
  }
  .backdiv {
  
  }
}
