.Wrapper {
  padding: 10% 0;
  overflow: hidden;
  color: white;
}

.Slider {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100% !important;
  z-index: -1;
  margin: 0 auto;
}
.Slider img {
  width: 100% !important;
}

.Wrapper h2 {
  font-size: 50px;
  width: 70%;
  font-weight: bold;
}
.Wrapper p {
  font-size: 20px;
}
.Wrapper button {
  background-color: #fcd318;
  color: black;
  border: 1px solid #fcd318;
  padding: 10px 20px;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  margin-top: 0.3%;
}
.Wrapper button a {
  color: black;
  text-decoration: none;
}
.backdiv {
  padding-bottom: 60px !important;
}
.backdi {
  padding-bottom: 120px;
}
@media screen and (max-width: 1040px) {
  .Wrapper {
    padding: 5% 2%;
  }
  .Wrapper h2 {
    font-size: 30px;
  }
}
@media screen and (max-width: 720px) {
  .Wrapper {
    padding: 5% 2%;
  }
  .Wrapper h2 {
    font-size: 30px;
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .Wrapper {
    padding: 10% 2% 10%;
  }
  .Wrapper h2 {
    font-size: 28px;
  }
  .Wrapper p {
    font-size: 16px;
  }
  .backdi {
    padding-bottom: 9% !important;
  }
  .backdiv {
    padding-bottom: 14% !important;
  }
  .backdv {
    padding-bottom: 5% !important;
  }
}
